.header {
  background: url(../images/headerBg.webp);
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  min-height: 900px;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 220px;
  position: relative;
}

.header__burger {
  position: fixed;
  width: 40px;
  height: 30px;
  transition: transform 900ms;
  display: none;
  top: 3rem;
  right: 1rem;
  overflow: hidden;
  text-align: left;
}

@media (max-width: 768px) {
  .header__burger {
    display: block;
    z-index: 100;
  }
}

.header__burger-line {
  background: #4985FF;
  height: 2px;
  position: absolute;
  width: 100%;
  transition: 400ms linear;
}

.header__nav {
  height: 100%;
  width: 100%;
  border-bottom: 1px solid #A1A1A1;
}

.header__nav-menu {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  max-width: 1115px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

@media (max-width: 1115px) {
  .header__nav-menu {
    max-width: 992px;
  }
}

@media (max-width: 992px) {
  .header__nav-menu {
    max-width: 768px;
  }
}

@media (max-width: 768px) {
  .header__nav-menu {
    max-width: 576px;
  }
}

@media (max-width: 576px) {
  .header__nav-menu {
    max-width: 100%;
  }
}

@media (max-width: 768px) {
  .header__nav-menu {
    position: fixed;
    z-index: 100;
  }
}

@media (max-width: 768px) {
  .header__nav-contents {
    transition: .3s;
    position: fixed;
    left: 0;
    top: 0;
    transform: scaleY(0);
    transform-origin: top;
    background: rgba(0, 0, 0, 0.665);
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: 15px;
    z-index: 10;
  }
}

.header__nav-list {
  display: flex;
  column-gap: 40px;
  justify-content: right;
  transition: opacity 1000ms ease;
}

@media (max-width: 768px) {
  .header__nav-list {
    flex-direction: column;
    align-items: center;
  }
}

.header__nav-list.active {
  transition: all 500ms ease;
}

.header__nav-link {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  transition: all 300ms;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #FFFFFF;
  padding-top: 40px;
  padding-bottom: 40px;
  height: 100%;
}

.header__nav-link::after {
  content: '';
  display: block;
  height: 0px;
  background: #FFFFFF;
  position: absolute;
  bottom: 0;
  width: 100%;
  transition: height 250ms;
}

.header__nav-link:hover.header__nav-link::after {
  height: 2px;
}

.header__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 45px;
}

.header__content-pretitle {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #FFFFFF;
}

.header__content-title {
  max-width: 830px;
  width: 100%;
  font-style: normal;
  font-weight: 800;
  font-size: 48px;
  line-height: 64px;
  text-align: center;
  color: #FFFFFF;
}

@media (max-width: 810px) {
  .header__content-title {
    max-width: 539px;
    font-size: 36px;
    line-height: 42px;
  }
}

@media (max-width: 520px) {
  .header__content-title {
    max-width: 339px;
  }
}

@media (max-width: 768px) {
  .header__burger.active ~ .header__nav-contents {
    transform: scaleY(1);
  }
}

.header__burger.active .header__burger-line {
  top: 50%;
  width: 100%;
}

.header__burger.active .header__burger-line:first-of-type {
  transform: translateY(-50%) rotate(-45deg);
}

.header__burger.active .header__burger-line:nth-of-type(2) {
  transform: translateX(110%);
}

.header__burger.active .header__burger-line:last-of-type {
  transform: translateY(-50%) rotate(-315deg);
}

.header__burger-line:first-child {
  top: 5%;
}

.header__burger-line:nth-of-type(2) {
  top: 50%;
  width: 80%;
}

.header__burger-line:nth-of-type(3) {
  bottom: 0%;
  transition: .5s;
}

